<template>
    <div class="box">
       
        <Header :got="true" :aot="false" :clt="true" :ant="false" :gnt="false"  :cot="false" :cat="false" :cnt="false" :cmt="false">
        </Header>
        <div class="content">
            <div class="item">
                <div class="group">
                    <div>
                        <van-cell-group>
                            <van-field v-model="username" clearable right-icon="question-o" class="inp" placeholder="请输入用户名"/>
                        </van-cell-group>
                    </div>
                    
                    <ul>
                        <li>
                            <van-cell-group>
                                <van-field :type="type" v-model="password"  class="pass" placeholder="请输入密码"/>
                            </van-cell-group>
                        </li>
                        <li><van-switch @change="bot"  v-model="checked" size="24px"/></li>
                    </ul>  
                </div>
                  
                <div class="inpss">
                    <ul>
                        <li>
                            <van-cell-group>
                                <van-field v-model="captcha_code" class="pass" placeholder="验证码" />
                            </van-cell-group>
                        </li>
                        <li v-if="code"><img :src="code" alt=""></li>
                        <li @click="clickImg">
                            <p>看不清</p>
                            <p>换一张</p>
                        </li>
                    </ul>
                </div>
                <div class="login">
                    <p>温馨提示：未注册过的账号，登录时将自动注册</p>
                    <p>注册过的用户可凭账号密码登录</p>
                    <van-button type="primary" @click="login" class="but">登录</van-button>
                    <div>重置密码?</div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import Header from '../header/Header'
    import axios from 'axios';
    import { Toast } from 'vant';
    axios.defaults.withCredentials=true;
    import {mapState,mapGetters,mapActions,mapMutations} from 'vuex'
    export default {
        components: {
          Header  
        },
        data() {
            return {
                username:"",
                password:"",
                code:"",
                captcha_code:"",
                user_id:"",
                checked:"",
                type:"password"
            }
        },
        mounted() {
            this.clickImg();
        },
        methods: {
             ...mapMutations([
                'meshash'
            ]),
            bot(){
                if(this.checked==true){
                  this.type="text" 
                }else{
                    this.type="password"
                }
            },
            clickImg(){
                axios.post("https://elm.cangdu.org/v1/captchas",{}).then(res => {
                    this.code = res.data.code;
                });
            },
            login(){
                axios.post("https://elm.cangdu.org/v2/login",{
                        username:this.username,
                        password:this.password,
                        captcha_code:this.captcha_code
                }).then((res)=>{
                    if(res.data.message){
                        this.$toast(res.data.message)
                    }else{
                        this.$store.commit('meshash',res.data)
                        Toast.loading({
                                mask: true,
                                message: '登录中...',
                                duration:1000
                        });
                        setTimeout(()=>{
                            this.$router.push('/myuser')
                        },1000)
                        
                    }
                })
               

            }
        }
    }
    
</script>

<style lang="scss" scoped>
.box{
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        font-size: 0.35rem;
    }
    .group{
        width: 100%;
        ul{
            width: 100%;
            height: 1rem;
            display: flex;
            li:nth-of-type(1){
                flex: 8;
            }
             li:nth-of-type(2){
                flex: 2;
                // line-height: 1rem;
                padding-top: 0.2rem;
            }
        }
    }
    .login{
        width: 100%;
        p{
            font-size: 0.28rem;
            padding: 0.2rem 0.3rem;
            color: rgb(150, 69, 69);
        }
        .but{
            width: 92%;
            // padding: 0.1rem;
            font-size: 0.4rem;
            // padding-left: 0.5rem;
            margin: 0.1rem 0.3rem;
        }
        div{
            width: 100%;
            // padding: 0.3rem 3.3rem;
            padding-left: 6rem;
            color: blueviolet;
            font-size: 0.3rem;
        }
    }
    .inpss{
        width: 100%;
        height: 1rem;
        ul{
            width: 100%;
            display: flex;
            li{
                height: 1rem;
            }
            li:nth-of-type(1){
                flex: 5;
                input{
                    height: 1rem;
                }
            }
            li:nth-of-type(2){
                flex: 1;
                // background: gray;
                padding-top: 0.23rem;
            }
            li:nth-of-type(3){
                flex: 1;
                font-size: 0.25rem;
                padding: 0 0.2rem;
                p{
                    line-height: 0.6rem;
                }
                p:nth-of-type(2){
                    color: blueviolet;
                }
            }
        }
    }
    .header{
        width: 100%;
        height: 1rem;
        background: rgb(192, 144, 236);
        line-height: 1rem;
        color: white;
        ul{
            width: 100%;
            display: flex;
            li{
                flex: 1;
                margin: 0 0.3rem;
                a{
                    color: white;
                }
                
            }
            li:nth-of-type(2){
                flex: 2;
                // text-align: center;
            }
        }
    }
    .content{
        width: 100%;
        flex: 1;
        overflow: auto;
    }
    .inp{
        height: 1.2rem;
        line-height: 0.8rem;
    }
    .pass{
        height: 1.2rem;
        line-height: 0.8rem;
    }
</style>


